import * as React from 'react';

export default
<semicolon>
    <clock name='dot1' c-clock={{tweenDuration: 2.0}} c-transform={{y: -28}}/>
    <clock name='dot2' c-clock={{tweenDuration: 2.0}} c-transform={{y:  28}}/>
</semicolon>;



